.progress__name{
	color: $white;
	font-family: 'Raleway';
	font-size: 14px;
	font-weight: 700;
	line-height: 20px;
	text-transform: uppercase;
	margin: 10px 0;
}
.progress__element {
	border: 1px solid rgba(255, 255, 255, 0.25);
	border-radius: 3px;
	padding: 2px;
}
.progress__line {
  background-color: $white;
  width: 0;
  height: 4px;
  position: relative;

}
.progress__tooltip {
  background-color: $primary;
  position: absolute;
  right: -15px;
  top: -32px;
  width: 31px;
	color: $white;
	font-size: 11px;
	font-weight: 700;
	line-height: 18px;
	text-align: center;
	border-radius: 4px;
	display: none;
	&::before{
		content: '';
		border: 4px solid transparent;
		border-top: 4px solid $primary;
		position: absolute;
		bottom: -7px;
		left: 50%;
		transform: translateX(-50%);
	}
}