$text-clor: #707070;

.menu{
	display: flex;
	list-style: none;
	flex-wrap: wrap;
}
.menu__element{
	margin-right: 1.25rem;
	&:last-child{
		margin-right: 0;
	}
}
.menu__link{
	color: $text-clor;
	font-family: "Raleway";
	font-size: 0.75em;
	font-weight: 400;
	line-height: 20px;
	text-transform: uppercase;
	display: inline-block;
	text-decoration: none;
	transition-duration: 500ms;
	transition-property: color;
	&::before{
		content: '';
		width: .5rem;
		height: 2px;
		background-color: $second;
		display: block;
		margin-bottom: 0.375rem;
		opacity: 0;
		transition-duration: 500ms;
		transition-property: opacity;
	}

	&:hover{
		color: $second;
		&::before{
			opacity: 1;
		}
	}
}

@media (min-width: 750px){
	.menu__element{
		margin-right: 2.5rem;
	}
}