$inputBg: #202020;

.form{

	label{
		@include visually-hidden;
	}
}
.form__input{
	margin-bottom: 20px;
	input, textarea{
	  border-radius: 10px;
	  background-color: $inputBg;
	  display: block;
	  width: 100%;
	  box-shadow: none;
	  border: none;
	  resize: none;
		color: $white;
		font-family: 'Raleway';
		font-size: 20px;
		padding: 16px 10px 16px 30px;
		font-weight: 500;
		line-height: 28px;
		box-sizing: border-box;
		&::placeholder{
			color: $white;
			opacity: 1;
		}
		
	}
	textarea{
			min-height: 200px;
	}
}
.form__button{
  border-radius: 10px;
  background-color: $second;
  width: 100%;
	box-shadow: none;
  border: none;
  appearance: none;
  cursor: pointer;
	color: $white;
	font-family: 'Raleway';
	font-size: 20px;
	font-weight: 700;
	line-height: 28px;
	text-transform: uppercase;
	padding: 16px 0;
	text-align: center;
	transition-property: background-color;
	transition-duration: 500ms;
		&:hover, &:active{
			background-color: darken($second, 15%);
		}
}