$lineColor: #2f2f2f;
$responsiveText: #464646;


.responsive__wrap{
	padding-top: 100px;
	padding-bottom: 100px;
	background: url('../img/bg-two.png') left bottom no-repeat;
}
.responsive__information{
	max-width: 350px;
	margin-left: auto;
}
.responsive__title{
	font-size: 30px;
	font-family: 'Raleway';
	color: $white;
	font-weight: 700;
	text-transform: uppercase;
	line-height: 1.1;
	margin: 25px 0;
	position: relative;
	&::before{
	  background-color: $lineColor;
	  position: absolute;
	  width: 70px;
	  height: 2px;
	  left: 0;
	  top: -25px;
	  content: '';
	}

}
.responsive__text{
  font-size: 14px;
  color: $responsiveText;
  line-height: 1.429;
  margin: 20px 0;
}
.responsive__list{
	margin: 20px 0;
}
.responsive__button{
	@include button($thirty) {
		margin-top: 50px;
	};
}
@media (max-width: 768px){
	.responsive__wrap{
		background-image: none;
	}
	.responsive__information{
		margin-left: 0;
		max-width: 100%;
	}
}
@media (max-width: 480px){
	.responsive__wrap{
		padding-top: 50px;
		padding-bottom: 50px;
	}
	.responsive__information{
		margin-left: 0;
		max-width: 100%;
	}
	.responsive__button{
		margin-right: auto;
		margin-left: auto;
	}
}