$circleBg: #363636;
$iconBg: #636363;
$colorTitle: #636363;
$colorText: #555555;
.advantages__title{
	@include visually-hidden;
}
.advantages__list{
	list-style: none;
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(210px, auto));
	grid-column-gap: 30px;
	padding-top: 65px;
	padding-bottom: 50px;
}
.advantages__item{
	margin: 15px 20px;
	&:hover{

		.advantages__img{
			border-color: $second;
		}
		.advantages__svg{
			fill: $second;
		}
		.advantages__subtitle{
			color: $second;
		}
		.advantages__text{
			color: $white;
		}
	}
}
.advantages__img {
	border: 1px solid $circleBg;
	border-radius: 50%;
	position: relative;
	width: 104px;
	height: 104px;
	margin-left: auto;
	margin-right: auto;
	text-decoration: 500ms;
	transition-property: border-color;
}
.advantages__svg{
	position: absolute;
	left: 50%;
	top: 50%;
	height: 40px;
	fill: $iconBg;
	transform: translate(-50%, -50%);
	text-decoration: 500ms;
	transition-property: fill;
}
.advantages__subtitle{
	color: $colorTitle;
	margin-top: 28px;
	margin-bottom: 20px;
	font-family: 'Raleway';
	font-size: 1.125em;
	font-weight: 800;
	line-height: 1.111;
	text-transform: uppercase;
	text-align: center;
	text-decoration: 500ms;
	transition-property: color;
}
.advantages__text{
	color: $colorText;
	font-family: 'Roboto';
	font-size: 14px;
	font-weight: 400;
	line-height: 1.111;
	margin-bottom: 28px;
	text-decoration: 500ms;
	transition-property: color;
	text-align: center;
	&::after{
		background-color: $second;
		width: 70px;
		height: 2px;
		content: '';
		display: block;
		margin-top: 30px;
		margin-left: 10px;
	}
}
@media (max-width: 480px){
	.advantages__list{
		padding-top: 30px;
		padding-bottom: 25px;
	}
}