$color-text: #555555;

.banner{
	background: none;
}
.banner__wrap{
	padding-top: 4.375rem;
	padding-bottom: 3.125rem;
}
.banner__info{
	max-width: 100%;
	margin-left: auto;
	margin-right: auto;
	text-align: center;
}
.banner__title{
	color: $white;
	font-family: 'Raleway';
	font-size: 2.5rem;
	font-weight: 800;
	line-height: 1;
	text-transform: uppercase;
	margin: 0;
}
.banner__little{
	font-size: 1.25rem;
	text-transform: uppercase;
	display: block;
}
.banner__red{
	color: $second;
}
.banner__text{
	color: $color-text;
	font-family: 'Raleway';
	font-size: 0.875rem;
	font-weight: 800;
	line-height: 24px;
	text-transform: uppercase;
	line-height: 1.3333;
	margin: 15px 0
}
.banner__button{
	@include button() {
	display: inline-block;
	margin-top: .625rem;
	margin-bottom: .625rem;
	margin-right: 0;
	};
}
.banner__button--last{
	margin-right: 0;
	border-color: $white;
	background-color: transparent;
}




@media (min-width: 22.5em){
	.banner__button{
		margin-right: 0;
	}
}
@media (min-width: 30em){
	.banner__title{
		font-size: 3.125rem;
	}
	.banner__little{
		font-size: 1.5625rem;
	}
	.banner__text{
		font-size: 1rem;
	}
	.banner__button{
		margin-right: 2.1875rem;
		margin-top: 1.875rem;
		margin-bottom: 1.25rem;
	}
	
}
@media (min-width: 48em){
	.banner__title{
		font-size: 5rem;
	}
	.banner__little{
		font-size: 2.6875rem;
	}
	.banner__text{
		font-size: 1.125rem;
	}
}

@media (min-width: 61.25em){
	.banner{
		background: url('../img/bg-man.jpg') center top no-repeat;
		background-size: cover;
	}
	.banner__wrap{
		padding-top: 16.25rem;
		padding-bottom: 16.25rem;
	}
	.banner__info{
		max-width: 36rem;
		margin-left: auto;
		margin-right: 0;
		text-align: inherit;
	}
}